.tables_container {
    margin: auto;
    padding: 3%;
}
.table_container {
    padding: 0 5%;
    min-width: 356px;
}

.stats_name {
    font-size: 115%;
}

.darker_icon {
    color:#EA8D47;
    font-size: 175%;
}

.lighter_icon {
    color: #ffbe75;
    font-size: 175%
}

.data {
    display: inline;
    margin: 0 15px 0 15px;
    border-style: solid;
    border-width: 1px;
    padding: 10px;
    font-size: 1.2em;
    background: white;
    /* position: relative;
    top: -4px; */
}

.stats_row {
    height: 60px;
}

.stats {
    text-align: center;
    min-width: 120px;
}

.null {
    display: inline;
    margin: 0 7% 0 7%;
    border-style: solid;
    padding: 8%;
    font-size: 1.2em;
}
.nice {
    display: inline;
    margin: 0 7% 0 7%;
    border-color: orange;
    border-style: solid;
    border-width: 2px;
    padding: 8%;
    font-size: 1.2em;
    /* color: white; */
    font-weight: bold;
    /* background: rgb(248,136,51, 0.5); */
    
}
.good {
    display: inline;
    margin: 0 7% 0 7%;
    border-color: orange;
    border-style: solid;
    border-width: 2px;
    padding: 8%;
    font-size: 1.2em;
    /* color: white; */
    font-weight: bold;
    /* background: rgb(255, 189, 107, 0.5) */
}
.full {
    width: 100%;
}
.h1_main {
    margin-bottom: 0%;
}
.player_stats {
    font-variant: small-caps;
}
.list_of_games {
    padding: 5%;
}


